    const sdsQuestions = ["ÊàëÊÑüÂà∞ÊÉÖÁª™‰ΩéËêΩ", "ÊàëËßâÂæóÊó©‰∏äÊúÄÂ•Ω", "ÊàëÁªèÂ∏∏Âì≠Ê≥£"];
    const sasQuestions = ["ÊàëÊÑüÂà∞ÊØîÂπ≥Êó∂ÂÆπÊòìÁ¥ßÂº†", "ÊàëÊó†ÁºòÊó†ÊïÖÊÑüÂà∞ÂÆ≥ÊÄï", "ÊàëÂÆπÊòìÂøÉÁÉ¶ÊÑè‰π±"];
    const sesQuestions = ["ÊàëÊÑüÂà∞ÊàëÊúâËÆ∏Â§ö‰ºòÁÇπ", "ÊàëÂ∏åÊúõËÉΩ‰ª•Ê≠§Â∞äÈáçËá™Â∑±", "ÊÄªÁöÑÊù•ËØ¥ÔºåÊàëÂÄæÂêë‰∫éËÆ§‰∏∫Ëá™Â∑±Â§±Ë¥•"];

