"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { toast } from 'sonner';
import { ClipboardList, History, Loader2, User } from 'lucide-react';
// ÂºïÁî®ÂàöÊâçÂÜôÁöÑ Actions Âíå Components
import { getCounselors, createAppointmentWithDetails, getAppointments, getCounselorSchedule } from '@/app/actions';
import PsychTestModal from '@/components/PsychTestModal';

export default function BookingPage() {
  const { currentUser } = useAppStore();
  const [activeTab, setActiveTab] = useState("new");
  
  const [counselors, setCounselors] = useState<any[]>([]);
  const [myAppointments, setMyAppointments] = useState<any[]>([]);
  
  // È¢ÑÁ∫¶ÈÄªËæëÁä∂ÊÄÅ
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [selectedTime, setSelectedTime] = useState<string>("");
  const [availableSlots, setAvailableSlots] = useState<string[]>([]);
  const [targetCounselorId, setTargetCounselorId] = useState<string | null>(null);
  const [loadingSlots, setLoadingSlots] = useState(false);
  
  // ‚úÖ ËØ¶ÁªÜË°®ÂçïÁä∂ÊÄÅ
  const [formData, setFormData] = useState({
    idCard: '', studentIdNum: '', phone: '', college: '', major: '', 
    grade: '', classInfo: '', mentalHistory: 'Êó†', physicalState: 'ËâØÂ•Ω', problemType: ''
  });

  const loadData = () => {
     if (currentUser?.id) {
      getCounselors().then(setCounselors);
      getAppointments(currentUser.id, 'student').then(setMyAppointments);
    }
  };

  useEffect(() => { loadData() }, [currentUser, activeTab]);

  // Êü•ÊéíÁè≠
  useEffect(() => {
    async function fetchSlots() {
      if (!targetCounselorId || !selectedDate) return;
      setLoadingSlots(true);
      setSelectedTime("");
      
      let dayOfWeek = selectedDate.getDay(); 
      if (dayOfWeek === 0) dayOfWeek = 7; 

      const res = await getCounselorSchedule(targetCounselorId);
      if (res.success) {
        const slotsForToday = res.data
          .filter((s: any) => s.dayOfWeek === dayOfWeek)
          .map((s: any) => s.timeSlot).sort();
        setAvailableSlots(slotsForToday);
      }
      setLoadingSlots(false);
    }
    fetchSlots();
  }, [targetCounselorId, selectedDate]);

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleBook = async () => {
    if (!selectedDate || !selectedTime || !formData.idCard || !formData.phone || !formData.problemType) {
      toast.error("ËØ∑ÂÆåÊï¥Â°´ÂÜôÊâÄÊúâÂøÖÂ°´È°πÔºàÂåÖÊã¨ÈóÆÈ¢òÁ±ªÂûãÔºâ");
      return;
    }

    const res = await createAppointmentWithDetails({
      studentId: currentUser!.id,
      counselorId: targetCounselorId,
      date: selectedDate.toISOString(),
      time: selectedTime,
      ...formData // Êèê‰∫§ÊâÄÊúâËØ¶ÊÉÖ
    });

    if (res.success) {
      toast.success("Áî≥ËØ∑Â∑≤Êèê‰∫§ÔºÅÁä∂ÊÄÅÔºöÂæÖÂÆ°Êâπ");
      setActiveTab("history");
      setTargetCounselorId(null);
      // ÈáçÁΩÆË°®Âçï
      setFormData({
        idCard: '', studentIdNum: '', phone: '', college: '', major: '', 
        grade: '', classInfo: '', mentalHistory: 'Êó†', physicalState: 'ËâØÂ•Ω', problemType: ''
      });
    } else {
      toast.error(res.msg || "È¢ÑÁ∫¶Â§±Ë¥•");
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold">ÂøÉÁêÜÂí®ËØ¢È¢ÑÁ∫¶</h1>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab}>
        <TabsList className="grid w-full grid-cols-2 lg:w-[400px]">
          <TabsTrigger value="new">ÂèëËµ∑È¢ÑÁ∫¶</TabsTrigger>
          <TabsTrigger value="history">ÊàëÁöÑËÆ∞ÂΩï</TabsTrigger>
        </TabsList>

        {/* --- È¢ÑÁ∫¶Tab --- */}
        <TabsContent value="new" className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
             {counselors.map((c) => (
                <div key={c.id} className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow flex flex-col gap-4">
                  <div className="flex items-center gap-4">
                     <div className="w-14 h-14 bg-slate-200 rounded-full flex items-center justify-center overflow-hidden">
                        {c.avatar ? <img src={c.avatar} className="w-full h-full object-cover"/> : <User className="text-slate-400"/>}
                     </div>
                     <div>
                         <h3 className="font-bold text-lg">{c.name}</h3>
                         <p className="text-sm text-slate-500">{c.college || 'ÂøÉÁêÜÂí®ËØ¢‰∏≠ÂøÉ'}</p>
                     </div>
                  </div>
                  
                  <Dialog onOpenChange={(open) => {
                      if(open) setTargetCounselorId(c.id);
                  }}>
                    <DialogTrigger asChild>
                        <Button className="w-full bg-[#5D9C59] hover:bg-[#4a8546]">Á´ãÂç≥È¢ÑÁ∫¶</Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
                      <DialogHeader>
                          <DialogTitle className="text-xl">È¢ÑÁ∫¶Áî≥ËØ∑Ë°® ({c.name} ËÄÅÂ∏à)</DialogTitle>
                      </DialogHeader>
                      
                      <div className="grid gap-6 py-4">
                        {/* Á¨¨‰∏ÄÈÉ®ÂàÜÔºöÂü∫Êú¨‰ø°ÊÅØ */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                            <h4 className="font-bold text-sm text-slate-700 border-b pb-2">1. Ë∫´‰ªΩ‰ø°ÊÅØ</h4>
                            <div className="grid grid-cols-2 gap-4">
                                <div className="space-y-2"><Label>ÂßìÂêç</Label><Input value={currentUser?.name} disabled className="bg-slate-100"/></div>
                                <div className="space-y-2"><Label>Â≠¶Âè∑ *</Label><Input placeholder="ÂøÖÂ°´" onChange={e=>handleInputChange('studentIdNum', e.target.value)}/></div>
                                <div className="space-y-2"><Label>Ë∫´‰ªΩËØÅÂè∑ *</Label><Input placeholder="ÂøÖÂ°´ (Áî®‰∫éÂª∫Ê°£)" onChange={e=>handleInputChange('idCard', e.target.value)}/></div>
                                <div className="space-y-2"><Label>ÊâãÊú∫Âè∑ *</Label><Input placeholder="ÂøÖÂ°´" onChange={e=>handleInputChange('phone', e.target.value)}/></div>
                                <div className="space-y-2"><Label>Â≠¶Èô¢</Label><Input onChange={e=>handleInputChange('college', e.target.value)}/></div>
                                <div className="space-y-2"><Label>‰∏ì‰∏ö</Label><Input onChange={e=>handleInputChange('major', e.target.value)}/></div>
                                <div className="space-y-2"><Label>Âπ¥Á∫ß</Label><Input onChange={e=>handleInputChange('grade', e.target.value)}/></div>
                                <div className="space-y-2"><Label>Áè≠Á∫ß</Label><Input onChange={e=>handleInputChange('classInfo', e.target.value)}/></div>
                            </div>
                        </div>

                        {/* Á¨¨‰∫åÈÉ®ÂàÜÔºöÂí®ËØ¢ËØ¶ÊÉÖ */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                            <h4 className="font-bold text-sm text-slate-700 border-b pb-2">2. Âí®ËØ¢ËØ¶ÊÉÖ</h4>
                            <div className="space-y-2">
                                <Label>Âí®ËØ¢ÈóÆÈ¢òÁ±ªÂûã *</Label>
                                <Select onValueChange={v=>handleInputChange('problemType', v)}>
                                    <SelectTrigger><SelectValue placeholder="ËØ∑ÈÄâÊã©" /></SelectTrigger>
                                    <SelectContent>
                                        <SelectItem value="Â≠¶‰∏öÂéãÂäõ">Â≠¶‰∏öÂéãÂäõ</SelectItem>
                                        <SelectItem value="‰∫∫ÈôÖÂÖ≥Á≥ª">‰∫∫ÈôÖÂÖ≥Á≥ª</SelectItem>
                                        <SelectItem value="ÊÉÖÊÑüÈóÆÈ¢ò">ÊÉÖÊÑüÈóÆÈ¢ò</SelectItem>
                                        <SelectItem value="ÊÉÖÁª™Âõ∞Êâ∞">ÊÉÖÁª™Âõ∞Êâ∞</SelectItem>
                                        <SelectItem value="ÁîüÊ∂ØËßÑÂàí">ÁîüÊ∂ØËßÑÂàí</SelectItem>
                                        <SelectItem value="Âç±Êú∫Âπ≤È¢Ñ">Âç±Êú∫Âπ≤È¢Ñ</SelectItem>
                                        <SelectItem value="ÂÖ∂‰ªñ">ÂÖ∂‰ªñ</SelectItem>
                                    </SelectContent>
                                </Select>
                            </div>
                            <div className="space-y-2">
                                <Label>‰∏™‰∫∫ÂèäÂÆ∂Â∫≠Á≤æÁ•ûÁñæÁóÖÂè≤</Label>
                                <Textarea placeholder="Â¶ÇÊúâÔºåËØ∑ÁÆÄËø∞ÔºõÂ¶ÇÊó†ÔºåËØ∑Â°´'Êó†'" onChange={e=>handleInputChange('mentalHistory', e.target.value)} />
                            </div>
                            <div className="space-y-2">
                                <Label>ÁõÆÂâçË∫´‰ΩìÂèäÂøÉÁêÜÁä∂ÊÄÅ</Label>
                                <Textarea placeholder="ÁÆÄËø∞ÁõÆÂâçÁöÑÁù°Áú†„ÄÅÈ•ÆÈ£ü„ÄÅÊÉÖÁª™Áä∂ÊÄÅ..." onChange={e=>handleInputChange('physicalState', e.target.value)} />
                            </div>
                        </div>

                        {/* Á¨¨‰∏âÈÉ®ÂàÜÔºöÊó∂Èó¥ÈÄâÊã© */}
                        <div className="bg-slate-50 p-4 rounded-lg space-y-4">
                            <h4 className="font-bold text-sm text-slate-700 border-b pb-2">3. È¢ÑÁ∫¶Êó∂Èó¥</h4>
                            <div className="flex flex-col md:flex-row gap-6">
                                <div className="border rounded-md bg-white p-2 flex justify-center">
                                    <Calendar mode="single" selected={selectedDate} onSelect={setSelectedDate} 
                                      disabled={(date) => date < new Date(new Date().setHours(0,0,0,0))}
                                    />
                                </div>
                                <div className="flex-1 space-y-4">
                                    <Label>ÈÄâÊã©ÂèØÁî®Êó∂ÊÆµ</Label>
                                    {loadingSlots ? (
                                        <div className="flex items-center gap-2 text-slate-500"><Loader2 className="animate-spin w-4 h-4"/> Êü•ËØ¢‰∏≠...</div>
                                    ) : availableSlots.length > 0 ? (
                                        <div className="grid grid-cols-2 gap-2">
                                            {availableSlots.map(slot => (
                                                <Button key={slot} variant={selectedTime === slot ? 'default' : 'outline'} 
                                                    className={selectedTime === slot ? 'bg-[#5D9C59]' : ''}
                                                    onClick={() => setSelectedTime(slot)}>
                                                    {slot}
                                                </Button>
                                            ))}
                                        </div>
                                    ) : (
                                        <p className="text-red-500 text-sm">ËØ•Êó•ÊúüÊöÇÊó†ÊéíÁè≠ÔºåËØ∑ÂàáÊç¢Êó•Êúü„ÄÇ</p>
                                    )}
                                </div>
                            </div>
                        </div>

                        <Button onClick={handleBook} disabled={!selectedTime} className="w-full bg-[#5D9C59] h-12 text-lg">
                            Êèê‰∫§È¢ÑÁ∫¶Áî≥ËØ∑
                        </Button>
                      </div>
                    </DialogContent>
                  </Dialog>
                </div>
             ))}
          </div>
        </TabsContent>

        {/* --- ÂéÜÂè≤ËÆ∞ÂΩïTab --- */}
        <TabsContent value="history" className="mt-6">
          <div className="space-y-4">
            {myAppointments.map((apt) => (
              <div key={apt.id} className="p-5 bg-white border rounded-xl shadow-sm flex flex-col md:flex-row justify-between items-center gap-4">
                <div className="flex items-center gap-4 w-full md:w-auto">
                  <div className="bg-slate-100 p-3 rounded-full"><History className="text-slate-500 w-6 h-6"/></div>
                  <div>
                    <div className="font-bold text-lg text-slate-800">{apt.counselor.name} ËÄÅÂ∏à</div>
                    <div className="text-sm text-slate-500 flex items-center gap-2 mt-1">
                       <Badge variant="outline">{apt.problemType || 'Â∏∏ËßÑÂí®ËØ¢'}</Badge>
                       <span>{new Date(apt.date).toLocaleDateString()} {apt.time}</span>
                    </div>
                  </div>
                </div>
                
                <div className="flex items-center gap-3 w-full md:w-auto justify-end">
                  {/* Áä∂ÊÄÅ1ÔºöÂæÖÂÆ°Êâπ */}
                  {apt.status === 'pending' && <Badge className="bg-blue-100 text-blue-700 hover:bg-blue-200 border-0 px-3 py-1">ÂæÖÂÆ°Êâπ</Badge>}
                  
                  {/* Áä∂ÊÄÅ2ÔºöÂ∑≤ÊãíÁªù */}
                  {apt.status === 'rejected' && <Badge variant="destructive" className="px-3 py-1">Â∑≤ÊãíÁªù</Badge>}
                  
                  {/* Áä∂ÊÄÅ3ÔºöÂ∑≤ÈÄöËøá‰ΩÜÊ≤°ÂÅöÊµãËØï (ÊòæÁ§∫ÊåâÈíÆ) */}
                  {apt.status === 'confirmed' && !apt.testResult && (
                      <div className="flex flex-col items-end gap-1">
                          <span className="text-xs text-orange-600 font-bold animate-pulse">ÂÆ°ÊâπÈÄöËøáÔºåËØ∑ÂÆåÊàêÂâçÊµã</span>
                          <PsychTestModal appointmentId={apt.id} onSuccess={loadData} />
                      </div>
                  )}

                  {/* Áä∂ÊÄÅ4ÔºöÂ∑≤ÂÆåÊàêÊµãËØï */}
                  {apt.status === 'confirmed' && apt.testResult && (
                      <div className="flex items-center gap-2">
                          <Badge className="bg-green-100 text-green-700 hover:bg-green-200 border-0 px-3 py-1 flex gap-1 items-center">
                              <ClipboardList className="w-3 h-3"/> ÊµãËØÑÂ∑≤ÂÆåÊàê
                          </Badge>
                          <Badge variant="outline" className="border-green-200 text-green-700">È¢ÑÁ∫¶ÊàêÂäü</Badge>
                      </div>
                  )}
                </div>
              </div>
            ))}
            {myAppointments.length === 0 && <div className="text-center py-12 text-slate-400">ÊöÇÊó†È¢ÑÁ∫¶ËÆ∞ÂΩï</div>}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}